:3rem;flex-wrap:wrap}.equipment-section .equipment-filters button{padding:.8rem 1.5rem;border:none;border-radius:30px;background:var(--glass-bg);color:var(--text-secondary);font-weight:500;transition:all .3s ease;backdrop-filter:blur(10px);border:1px solid var(--glass-border)}.equipment-section .equipment-filters button:hover,.equipment-section .equipment-filters button.active{background:var(--gradient-primary);color:#fff;transform:translateY(-2px)}.equipment-section .equipment-grid{display:grid;grid-template-columns:repeat(auto-fit, minmax(280px, 1fr));gap:2rem;padding:0 2rem}.equipment-section .equipment-grid .equipment-card{background:var(--glass-bg);backdrop-filter:blur(12px);border:1px solid var(--glass-border);box-shadow:0 8px 32px rgba(0,0,0,.3);padding:2rem;border-radius:20px;transition:all .4s ease;animation:fadeIn .6s ease forwards}.equipment-section .equipment-grid .equipment-card:hover{transform:translateY(-5px) scale(1.02)}.equipment-section .equipment-grid .equipment-card h4{font-size:1.5rem;margin-bottom:1rem}.equipment-section .equipment-grid .equipment-card p{color:var(--text-secondary);margin-bottom:.5rem}.equipment-section .equipment-grid .equipment-card .specs{color:var(--text-muted);font-size:.9rem}